import { decodeEntities } from '@wordpress/html-entities';

const { registerPaymentMethod } = window.wc.wcBlocksRegistry
const { getSetting } = window.wc.wcSettings

const settings = getSetting('stripe_hosted_gateway_data', {})
console.log(settings);
const label = decodeEntities(settings.title);
const checkoutbuttontext = decodeEntities(settings.checkoutbuttontext);

const Content = () => {
    let description = settings.description;

    if (settings.testmode === true) {
        description += '<br><br><h3>TEST MODE ENABLED.</h3>';
    }

    return <div className="description" dangerouslySetInnerHTML={{ __html: description }}></div>

}

const EditContent = () => {
    let description = settings.description;

    if (settings.testmode === true) {
        description += '<br><br><h3>TEST MODE ENABLED TEST.</h3>';
    }

    return <div className="description" dangerouslySetInnerHTML={{ __html: description }}></div>

}


const Icon = () => {
    return settings.icon
        ? <img src={settings.icon} style={{ float: 'right', marginRight: '20px' }} />
        : ''
}
const Label = () => {
    return (
        <span style={{ width: '100%' }}>
            {label}
            <Icon />
        </span>
    )
}



registerPaymentMethod({
    name: "stripe_hosted_gateway",
    label: <Label />,
    content: <Content />,
    edit: <EditContent />,
    canMakePayment: () => true,
    ariaLabel: label,
    placeOrderButtonLabel: checkoutbuttontext,
    supports: {
        features: settings.supports,
    }
})